#info-panel {
    text-align: left;
}

#info-panel h4 {
    margin-left: 1em;
}

.section-header {
    text-align: center;
    text-decoration: underline;
}

@media only screen and (min-width: 960px) {
    #info-panel {
        grid-area: 2 / 1 / 3 / 2;
        height: 350px;
    }
}